.recipe-container {
    grid-area: 1 / 2 / 5 / 6;
    padding: 0 2em;
    gap: 2em;
    margin-bottom: 3em;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}



.recipe {
    cursor: pointer;
    width: 20%;
    min-width: 250px;
    height: 60px;
    @include box;
}